/* 인트로 */
.body {
  overflow: hidden;
  background-color: #121212;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}
.body.on{
  animation: loading ease-out 3s;
}
@keyframes loading {
  0%{opacity: 1; transform: scale(1);}
  99%{transform: scale(10);}
  100%{transform: scale(10); opacity: 0; display: none;}
}

.loading{
  display: block;
  width: 200px; height: 100px;
  position: absolute; top: 65%; left: 49%;
  transform: translateX(-50%);
  font-size: 40px;
  color: #fff;
  letter-spacing: -1px;
  font-family: 'DungGeunMo';
  background: none;
}

#hourglass {
  position: absolute; top: 67%; left: 51%;
  transform: scale(0.5);
  height: 88px;
  width: 52px;
  animation: flip 6s steps(8) infinite;
}
#hourglass div {
  background: #000;
  position: absolute;
  width: 4px;
  height: 4px;
  transform-origin: center top;
}
