#hourglass .curtain {
  background: #fff;
  top: 12px;
  left: 8px;
  height: 24px;
  width: 36px;
  z-index: 3;
  animation: sandEmpty 6s steps(1) infinite;
}
#hourglass .curtain:nth-of-type(3) {
  top: 52px;
  animation: sandFill 6s steps(1) infinite;
}
#hourglass .sand-fall {
  background: transparent;
  box-shadow: 24px 28px #000;
  z-index: 5;
  animation: sandFall 6s steps(11) infinite;
}
#hourglass .sand-fall:nth-of-type(5) {
  animation-delay: 0.5333333333s;
}
#hourglass .sand {
  box-shadow: 8px 12px #000, 16px 12px #000, 24px 12px #000, 32px 12px #000, 40px 12px #000, 12px 16px #000, 20px 16px #000, 28px 16px #000, 36px 16px #000, 16px 20px #000, 24px 20px #000, 32px 20px #000, 20px 24px #000, 28px 24px #000, 24px 28px #000, 8px 72px #000, 16px 72px #000, 24px 72px #000, 32px 72px #000, 40px 72px #000, 12px 68px #000, 20px 68px #000, 28px 68px #000, 36px 68px #000, 16px 64px #000, 24px 64px #000, 32px 64px #000, 20px 60px #000, 28px 60px #000, 24px 56px #000;
  z-index: 2;
}
@keyframes sandFall {
  from {
    transform: translateY(0);
    visibility: visible;
  }
  16.667% {
    transform: translateY(44px);
  }
  16.668% {
    transform: translateY(0);
  }
  31.819% {
    transform: translateY(40px);
  }
  31.82% {
    transform: translateY(0);
  }
  45.455% {
    transform: translateY(36px);
  }
  45.456% {
    transform: translateY(0);
  }
  57.576% {
    transform: translateY(32px);
  }
  57.577% {
    transform: translateY(0);
  }
  68.182% {
    transform: translateY(28px);
    visibility: visible;
  }
  68.183% {
    transform: translateY(28px);
    visibility: hidden;
  }
  to {
    transform: translateY(28px);
    visibility: hidden;
  }
}
@keyframes sandEmpty {
  from {
    transform: scale(1, 0);
  }
  16.667% {
    transform: scale(1, 0.167);
  }
  31.819% {
    transform: scale(1, 0.333);
  }
  45.455% {
    transform: scale(1, 0.5);
  }
  57.576% {
    transform: scale(1, 0.667);
  }
  68.182% {
    transform: scale(1, 0.833);
  }
  100% {
    transform: scale(1, 1);
  }
}
@keyframes sandFill {
  from {
    transform: scale(1, 1);
  }
  16.667% {
    transform: scale(1, 0.833);
  }
  31.819% {
    transform: scale(1, 0.667);
  }
  45.455% {
    transform: scale(1, 0.5);
  }
  57.576% {
    transform: scale(1, 0.333);
  }
  68.182% {
    transform: scale(1, 0.167);
  }
  100% {
    transform: scale(1, 0);
  }
}

