/* 메인 */
.main{
  width: 100vw; height: 400vh;
  display: flex;
  flex-direction: column;
}


.background{
  position: absolute;
  width: 100vw; height: 400vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.topBtn{
  position: absolute; bottom: 0; left: 50%;
  transform: translateX(-50%);
  background-color: #3b3b3b;
}
.topBtn>button{
  background-color: transparent;
  border: 1px solid #b2b2b2;
  padding: 10px 20px;
  border-radius: 5px;
  font-family: 'DungGeunMo';
  font-size: 1.2rem;
  color: #eaeaea;
  cursor: pointer;
}